
	svg {
		width: 300px;
		height: 100%;
	}
	.clock-face {
		stroke: #333;
		fill: white;
	}
	.minor {
		stroke: #999;
		stroke-width: 0.5;
	}
	.major {
		stroke: #333;
		stroke-width: 1;
	}
	.hour {
		stroke: #333;
	}
	.minute {
		stroke: #666;
	}
	.second, .second-counterweight {
		stroke: rgb(180,0,0);
	}
	.second-counterweight {
		stroke-width: 3;
	}


